<template>
  <transition name="fade">
    <section class="msg">
      <div class="msg_wrapper">
        <div class="msg_musk"></div>
        <div class="msg_box box">
          <header class="box_header">
            <span class="box_title">微科技</span>
            <i
              @click="cancelBtnClicked"
              class="box_close el-icon-close"
            ></i>
          </header>
          <div class="box_content">
            <div class="box_criteria">
              <div class="criteria_title">
                配置条件
              </div>
              <div class="criteria_content">
                <p v-if="codeData.activityId=='ALL'">适用活动:不限</p>
                <p v-else>适用活动:{{codeData.activityName}}</p>
                <p>有效性:{{codeData.useNumber}}次有效</p>
                <!-- <p v-else>有效性:{{codeData.sureUseNumber}}次有效</p> -->
                <p>参赛码有效期:{{codeData.startDate | getdate}}~{{codeData.endDate | getdate}}</p>
              </div>
            </div>
            <div class="box_list">
              <div class="list_title">
                参赛码列表
              </div>
              <div class="list_content">
                <div class="copy_btn" v-if="!isShowConfirm"  type="text" size="medium"
                  v-clipboard:copy="sysAppIds"
                  v-clipboard:success="onCopy"
                  v-clipboard:error="onError">
                  复制全部
                </div>
                <ul v-if="isShowConfirm" >
                  <li v-for="(item,index) in codeData.codeNames" :key="index">
                    <span class="name">参赛码{{index+1}}</span>{{item}}
                  </li>
                </ul>
                <ul v-else>
                  <li v-for="(item,index) in codeArray" :key="index">
                    <span class="name">参赛码{{index+1}}</span>{{item.codeName}}
                    <span class="sign" v-if="item.sureUseNumber == 0 || judgeStatus(item.endDate) == false">失效</span>
                  </li>
                </ul>
              </div>
            </div>
            <div class="button" v-if="isShowConfirm">
              <div class="cancle_btn" @click="cancelBtnClicked">
                取消
              </div>
              <div class="save_btn" @click="comfirmBtnClicked">
                保存
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </transition>
</template>

<script>
import {mapState} from 'vuex';
export default {
  name: "message-box",
  props: {
    codeArray:{
      type:Array
    },
    codeData:{
      type:Object
    },
    isShowConfirm:{
      type: Boolean,
    },  
  },
  data() {
    return {
      sysAppIds: '',//复制参赛码集合
    }
  },
  computed: {
    ...mapState({
      userInfo:(state)=>state.mutations.userInfo
    })
  },
  created() {
    //查看详情
    if(!this.isShowConfirm){
      this.manageCodeData()
    }
  },
  methods: {
    //判断状态
    judgeStatus(time){
      let nowTime = new Date().getTime();
      if(time-nowTime>0){
        return true
      }else{
        return false
      }
    },
    comfirmBtnClicked() {
      this.saveCode()
    },
    cancelBtnClicked() {
      this.$emit("cancelBtnClicked",false);
    },
    saveCode(){
      let url = this.url + this.net.saveCodeAll;
      let userId = this.userInfo.id;
      let params = {
        "activityId":this.codeData.activityId,
        "startDate":this.codeData.startDate,
        "endDate":this.codeData.endDate,
        "createUser":userId,
        "useNumber":this.codeData.useNumber,
        "number": this.codeData.number,
        "codeNames": this.codeData.codeNames
      }
      this.$post(url,params).then((res)=>{
        console.log('sucess:',res)
        if(res.code == 200 ){
          this.$message({
            message: '保存成功!',
            type: 'success'
          });
          this.$emit("cancelBtnClicked",false);
          this.$emit("isSaveCode",true)
        }else{
          this.$message({
            message: '数据已存在，禁止重复保存!',
            type: 'warning'
          });
        }
      }).catch((err)=>{
        console.log('err:',err)
      })

    },
    manageCodeData(){
      let arr = []
      this.codeArray.forEach((item,index) => {
        arr.push(item.codeName)
      });
      this.sysAppIds = arr.join(' ')
    },
    //复制成功
    onCopy(e){
      if(e){
        this.$message({
          message: '复制成功!',
          type: 'success'
        });
      }
    },
    // 复制失败
    onError(err){
      this.$message.error('复制失败!');
    },
  }
}
</script>
<style lang="less" scoped>
.msg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  .msg_box {
    width: 30%;
    //max-height: 460px;
    z-index: 1;
    position: absolute;
    top: 50%;
    margin-top: 65px;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #ffffff;
    border-radius: 4px;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    padding-bottom: 60px;
    .box_header {
      height: 34px;
      line-height: 34px;
      padding-left: 30px;
      background-color: #e9e9e9;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .box_title {
        font-size: 16px;
      }
      .box_close {
        padding: 7px;
        font-size: 20px;
        cursor: pointer;
        transition: all 0.1s;
      }
      .box_close:hover {
        background-color: #ccc;
      }
    }
    .box_content{
      padding: 20px 0 20px 60px;
      font-size: 14px;
      color: #8d8d8d;
      position: relative;
      .box_criteria{
        .criteria_title{
          height: 50px;
          line-height: 50px;
          font-size: 16px;
          border-bottom: 1px solid #eeeeee;
          font-weight: bold;
        }
        .criteria_content{
          p{
            margin-top: 6px;
          }
        }
      }
      .box_list{
        margin-top: 30px;
        .list_title{
          height: 50px;
          line-height: 50px;
          font-size: 16px;
          font-weight: bold;
          border-bottom: 1px solid #eeeeee;
        }
        .list_content{
          position: relative;
          ul{
            max-height: 200px;
            overflow-y: scroll;
            li{
              margin-top: 8px;
              .name{
                font-weight: bold;
                margin-right: 15px;
              }
              .sign{
                background-color: #e9e9e9;
                border-radius: 2.5px;
                border: 1px solid #dadada;
                width: 39px;
                height: 15px;
                font-size: 12px;
                padding: 2px 5px;
                margin-left: 12px;
              }
            }
          }
          .copy_btn{
            width: 80px;
            height: 20px;
            line-height: 20px;
            text-align: center;
            background-color: #e9e9e9;
            font-size: 12px;
            color: #8d8d8d;
            position: absolute;
            top: 10px;
            right: 55px;
            cursor: pointer;
          }
          .copy_btn:hover{
            opacity: 0.8;
          }
        }
      }
      .button{
        position: absolute;
        bottom: -30px;
        right: 55px;
        display: flex;
        .cancle_btn,.save_btn{
          width: 60px;
          height: 20px;
          line-height: 20px;
          text-align: center;
          background-color: #e9e9e9;
          font-size: 12px;
          color: #8d8d8d;
          border-radius: 2.5px;
          cursor: pointer;
        }
        .save_btn{
          background-color: #4cb4e7;
          color: #ffffff;
          margin-left:20px;
        }
      }
    }
  }
  .msg_musk {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    background-color: rgba(198, 203, 212, .67);
  }

}
.disabled {
  opacity: 0.5;
}










</style>



