<template>
  <div class="matchAllocation">
    <div class="container">
      <div class="activeTitle">
        参赛码配置
      </div>
      <div class="settingContent">
        <div class="settingOptions">
          <div class="title">
            配置
          </div>
          <div class="contents">
            <div class="requirement">
              <span>
                通用活动
                <el-select :clearable="clearable" v-model="activeValue" placeholder="请选择" class="selected" @change="selectActivity">
                  <el-option   
                  v-for="item in activeOptions"
                  :key="item.activityId"
                  :label="item.activityName"
                  :value="item.activityName">
                  </el-option>
                </el-select>
              </span>
              <span>
                有效性
                <el-select v-model="effectiveValue" placeholder="请选择" class="selected" @change="selectFrom">
                <el-option
                v-for="item in effectiveOptions"
                :key="item.value2"
                :label="item.label2"
                :value="item.label2">
                </el-option>
              </el-select>
              </span>
              <span class="staticNums">
                数量
                <el-input v-model.trim="inputNums" placeholder="1~999" :maxlength='3'></el-input>
              </span>
              <span>
                有效期
                <el-date-picker
                  v-model="timeValue"
                  type="datetimerange"
                  range-separator="至"
                  start-placeholder="开始日期"
                  @change="selectTime"
                  value-format="timestamp"
                  end-placeholder="结束日期">
                </el-date-picker>
              </span>
            </div>
            <div class="submitBtn" @click="createCode">
              生成
            </div>
          </div>
        </div>
        <div class="settingHistory">
          <div class="title">
            历史参赛码
          </div>
          <div class="list">
            <div class="noData" v-if="!historyCode[0]">
              暂无数据
            </div>
            <ul class="clearfix">
              <li v-for="(item,index) in historyCode" :key="index" @click="getCodeDetail(item)">
                <div class="listNum">
                  <div class="num" :class="{commonClass:judgeStatus(item.startDate,item.endDate)=='已过期'}">{{index+1}}</div>
                  <p :class="{commonClass1:judgeStatus(item.startDate,item.endDate)=='已过期'}"></p>
                </div>
                <div class="listText" :class="{commonClass2:judgeStatus(item.startDate,item.endDate)=='已过期'}">
                  <div class="listTextC" >
                    <div class="listTextL">
                      <div class="listTitle">
                        适用活动
                      </div>
                      <div class="listName" v-if="item.activityId=='ALL'">
                          不限
                      </div>
                      <div class="listName" v-else>
                        {{item.activityName}}
                      </div>
                      <div class="report">
                        状态
                      </div>
                      <div class="reportor">
                        {{judgeStatus(item.startDate,item.endDate)}}
                      </div>
                      <div class="date">
                        {{item.startDate | getDate}}~{{item.endDate | getDate}}
                      </div>
                    </div>
                  </div>
                </div>
                <div class="closeBtn" @click.stop="showConfimBox(item.createTime)">
                  <i class="el-icon-close"></i>
                </div>
              </li>

              <!-- <li >
                <div class="listNum">
                  <div class="num" >1</div>
                  <p></p>
                </div>
                <div class="listText" >
                  <div class="listTextC" >
                    <div class="listTextL">
                      <div class="listTitle">
                        适用活动
                      </div>
                      <div class="listName">
                        适用活动适用活动适用活动适用活动适用活动
                      </div>
                      <div class="report">
                        状态
                      </div>
                      <div class="reportor">
                        生效中
                      </div>
                      <div class="date">
                        2017
                      </div>
                    </div>
                  </div>
                </div>
                <div class="closeBtn" >
                  <i class="el-icon-close"></i>
                </div>
              </li> -->
            </ul>
	        </div>
        </div>
      </div>
    </div>
    <messageBox v-if="isShowMessage" @cancelBtnClicked='cancelBtnClicked' @isSaveCode='isSaveCode' :isShowConfirm='isShowConfirm' :codeData='codeData' :codeArray='codeArray'></messageBox>
  </div>
</template>
<style lang="less" scoped>
.matchAllocation{
   text-align: left;
   background-color: #f5f5f5;
  .container{
    margin:60px 0 0 260px;
    .activeTitle{
      color: #808080;
  	  height: 58px;
  	  font-size: 14px;
  	  line-height: 58px;
  	  padding-left: 50px;
  	  border-bottom: 1px solid #eee;
    }
    .settingContent{
      background-color: #f5f5f5;
      padding: 20px 50px;
      .settingOptions{
        height: 264px;
        background-color: #fff;
        border-radius: 11.5px;
        border: 1px solid #d3d3d3;
        position: relative;
        .title{
          height: 56px;
          line-height: 56px;
          font-size: 20px;
          color: #0d0d23;
          border-bottom: 1px solid #d3d3d3;
          padding-left: 43px;
        }
        .contents{
           padding:20px 50px;
          .requirement{
            text-align: left;
            font-size: 12px;
            span{
              margin-right: 20px;
              display: inline-block;
              margin-bottom: 20px;
              .selected{
                margin-left: 8px;
                width: 155px;
                height: 27px;
              }
              .selected:first-child{
                width: 122px;
              }
              .el-date-editor {
                 margin-left: 8px;
              }
            }
          }
          .submitBtn{
            background-color: #00c5e2;
            border-radius: 4px;
            width: 100px;
            height: 32px;
            font-size: 16px;
            color: #ffffff;
            text-align: center;
            line-height: 32px;
            cursor: pointer;
            position: absolute;
            left: 50px;
            bottom:25px;
          }
        }
      }
      .settingHistory{
        background-color: #fff;
        border-radius: 11.5px;
        border: 1px solid #d3d3d3;
        margin-top: 27px;
        .title{
          height: 56px;
          line-height: 56px;
          font-size: 20px;
          color: #0d0d23;
          border-bottom: 1px solid #d3d3d3;
          padding-left: 43px;
        }
        .list{
          padding: 20px 50px;
          ul{
            margin-left: 0;
            padding-left: 0;
            li{
              width: 300px;
              list-style: none;
              margin-left: 0;
              display: flex;
              height: 150px;
              box-shadow: 0 0 2px #ccc;
              margin-bottom: 20px;
              float: left;
              margin-right: 40px;
              position: relative;
              .listNum{
                width: 100px;
                text-align: center;
                .num{
                  width: 30px;
                  height: 30px;
                  margin: 0px auto;
                  margin-top: 50px;
                  border-radius: 100%;
                  border:2px solid #4cb4e7;
                  text-align: center;
                  line-height: 30px;
                  font-size: 16px;
                  color: #4cb4e7;
                  font-weight: bold;
                }
                p{
                  width: 24px;
                  height: 2px;
                  background: #4cb4e7;
                  box-shadow: 0 0 2px #4cb4e7;
                  margin:4px auto;
                }
                .listMark{
                  margin-top: 10px;
                  font-size: 12px;
                }
              }
              .listText{
                cursor: pointer;
                background: rgb(232,232,231);
                padding: 10px;
                position: relative;
                .blogDelete{
                  background: red;
                  position: absolute;
                  right: 0;
                  top: 0;
                  padding: 2px 6px;
                  color: white;
                  text-align: center;
                  font-size: 12px;
                }
                .listTextC{
                  width: 200px;
                  font-size: 14px;
                  color: #707070;
                  .listName{
                    font-size: 15px;
                    font-weight: bold;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    margin-bottom: 8px;
                  }
                  .reportor{
                    font-weight: bold;
                    margin-bottom: 8px;
                  }
                }
                .listTextL{
                  width: 100%;
                  margin-top: 7px;
                }
              }
              .closeBtn{
                width: 12px;
                height: 12px;
                position: absolute;
                right: 14px;
                top:8px;
                cursor: pointer;
                .el-icon-close{
                  color: #c4c4c4;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>
<script>
import messageBox from '@/components/messageBox'
import {mapState} from 'vuex';
const effectiveOptions = [{
          value2: 1,
          label2: '仅一次'
        },{
          value2: 3,
          label2: '三次'
        },{
          value2: 5,
          label2: '五次'
        },{
          value2: 10,
          label2: '十次'
        },{
          value2: 15,
          label2: '十五次'
        }]

export default {
  name:'matchAllocation',
  data(){
    return{
      isShowMessage:false,
      isShowConfirm:true,//判断是查看还是生成
      inputNums:'',//数量
      activeOptions: [
        { 
          "activityId":"ALL",
          "activityName":"不限"
        }
      ],//活动选项
      activeValue: '不限',
      activityId:'ALL',
      activityName:'不限',
      effectiveOptions: effectiveOptions,//有效选项
      effectiveValue: '仅一次',
      effectiveTimes: '1',
      timeValue: '',
      clearable: true,
      codeData:{},//参赛码
      codeArray:[],//详情参赛码
      historyCode:[],//历史数据
    }
  },
  watch: {
    inputNums (val) {
       if(val!= "" && !this.IsInteger(Number(val))){
          this.$message({
            message:'请输入正确的整数值!',
            type: 'warning'
          })
       } 
    }
  },
  computed: {
    ...mapState({
      userInfo:(state)=>state.mutations.userInfo
    })
  },
  created() {
    this.getActivityPager()
    this.queryHistoryCode()
  },
  methods: {
    //判断数值是否为整数
    IsInteger(obj) {
      return typeof obj === 'number' && obj%1 === 0 && obj>0
    },
    isSaveCode(val){
      if(val){
        this.queryHistoryCode()
      }
    },
    cancelBtnClicked(val){
      this.isShowMessage = val
    },
    //选择活动
    selectActivity (val) {
      this.activityName = val;
      this.activeOptions.find((item)=>{
        if(item.activityName == val){
          this.activityId = item.activityId
        }
      });
    },
    //选择有效性
    selectFrom(val) {
      this.effectiveValue = val
      this.effectiveOptions.find((item)=>{
        if(item.label2 == val){
          this.effectiveTimes = item.value2
        }
      })
    },
    //选择时间
    selectTime(val) {
      this.timeValue = val
    },
    showCodeInfo() {
      this.isShowMessage = true,
      this.isShowConfirm = true
    },
    //判断状态
    judgeStatus(starTime,endTime){
      let nowTime = new Date().getTime();
      if(starTime>nowTime){
        return '未开始'
      }else if(endTime > nowTime){
        return '生效中'
      }else if(endTime < nowTime){
        return '已过期'
      }
    },
    //获取活动列表
    getActivityPager(){
      let url = this.url + this.net.queryActivityPager;
      let params = {
        "pageNum":1,
        "pageSize":100,
        "orderByParam":"{auditState:1}"
      }
      this.$post(url,params).then((res)=>{
        if(res.code == 200 && res.data.result != null && res.data.result.length > 0){
          // let obj = {
          //   "activityId":"ALL",
				  //   "activityName":"不限",
          // }
          //let newArrLength = res.data.result.unshift(obj);
          //this.activeOptions = res.data.result;
          //this.activeValue = res.data.result[0].activityName;
          //this.activityId = res.data.result[0].activityId;
          //this.activityName = res.data.result[0].activityName
          let activeData = res.data.result;
          activeData.forEach(item => {
            this.activeOptions.push(item)
          });
        }
      }).catch((err)=>{
        console.log('err:',err)
      })
    },
   
    //创建参赛码
    createCode(){
      let url = this.url + this.net.createCode;
      let userId = this.userInfo.id;
      let time = this.timeValue;
      let nowTime = new Date().getTime();//当前时间
      if(this.activityId == ''){
        this.$message({
          message: '请先添加活动',
          type: 'warning'
        });
        return;
      }else if(this.effectiveTimes == ''){
        this.$message({
          message: '请选择有效次数',
          type: 'warning'
        });
        return;
      }else if(this.inputNums == ''){
        this.$message({
          message: '请填写数量',
          type: 'warning'
        });
        return;
      }else if(!time[0]){
        this.$message({
          message: '请选择时间',
          type: 'warning'
        });
        return;
      }else if(time[1]<nowTime){
        this.$message({
          message: '截止日期不能小于当前时间',
          type: 'warning'
        });
        return;
      }
      let params = {
        "activityId":this.activityId,
        "startDate":time[0],
        "endDate":time[1],
        "activityName":this.activityName,
        "createUser":userId,
        "useNumber":this.effectiveTimes,
        "number": this.inputNums 
      }
      this.$post(url,params).then((res)=>{
        if(res.code == 200 ){
          this.codeData = res.data;
          this.showCodeInfo()
        }
      }).catch((err)=>{
        console.log('err:',err)
      })
    },
    //获取历史参赛码
    queryHistoryCode(){
      let url = this.url + this.net.queryHistoryCode;
      this.$post(url,{}).then((res)=>{
        console.log('sucess:',res)
        if(res.code == 200 && res.data.result != null && res.data.result != 'null'){
          this.historyCode = res.data.result
        }
      }).catch((err)=>{
        console.log('err:',err)
      })
    },
    //获取参赛码详情
    getCodeDetail(item){
      console.log(item)
       let url = this.url + this.net.queryHistoryCodeDetails;
       let params = {
        "pageNum":1,
	      "pageSize":100,
        "createTime":item.createTime,
      }
      this.$post(url,params).then((res)=>{
        console.log('sucess:',res)
        if(res.code == 200 ){
          this.codeData = item;
          this.codeArray = res.data.result;
          this.isShowMessage = true;
          this.isShowConfirm = false
        }
      }).catch((err)=>{
        console.log('err:',err)
      })
    },
     //删除活动
    DeleteItems(createTime){
      let url = this.url + this.net.removeCode;
      let params = {
        "createTime":createTime,
      }
      this.$post(url,params).then((res)=>{
        if(res.code == 200 && res.data == true){
          this.historyCode.forEach((item,index) => {
            if(item.createTime == createTime){
              this.historyCode.splice(index,1)
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          });
        }
      }).catch((err)=>{
        console.log('err:',err)
      })
    },
    showConfimBox(createTime){
      this.$confirm('该组参赛码都将失效，确认删除吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.DeleteItems(createTime)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });          
      });
    }
  },
  components:{
    messageBox,
  }
}
</script>


